import React, { useState } from "react";
import DashboardSidebar from "../sidebar/sidebar.jsx";
import { Button, Modal } from "react-bootstrap";
import StickyBox from "react-sticky-box";
import { Link } from "react-router-dom";
import CloseButton from "react-bootstrap/CloseButton";

const Specialities = () => {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <div>
      <div className="content">
        <div className="container-fluid">
          <div className="row">
            <div className="col-md-5 col-lg-4 col-xl-3 theiaStickySidebar">
              <StickyBox offsetTop={20} offsetBottom={20}>
                <DashboardSidebar />
              </StickyBox>
            </div>

            <div className="col-md-7 col-lg-8 col-xl-9">
              <div className="row">
                <div className="col-sm-12">
                  <div className="card">
                    <div className="card-header">
                      <h4 className="card-title float-left">Specialities</h4>
                      <a
                        href="#modal_medical_form"
                        className="btn btn-primary float-right"
                        onClick={() => handleShow("medical_details")}
                        data-toggle="modal"
                      >
                        Add Speciality
                      </a>
                    </div>
                    <div className="card-body ">
                      {/* Dependent Tab */}
                      <div className="card card-table mb-0">
                        <div className="card-body">
                          <div className="table-responsive">
                            <table className="table table-hover table-center mb-0">
                              <thead>
                                <tr>
                                  <th>#</th>
                                  <th>Name</th>
                                  <th>BMI</th>
                                  <th className="text-center">Heart Rate</th>
                                  <th className="text-center">FBC Status</th>
                                  <th>Weight</th>
                                  <th>Order date</th>
                                  <th>Action</th>
                                </tr>
                              </thead>
                              <tbody>
                                <tr>
                                  <td>1</td>
                                  <td>Richard Wilson</td>
                                  <td>23.7</td>
                                  <td className="text-center">89</td>
                                  <td className="text-center">140</td>
                                  <td>74Kg</td>
                                  <td>
                                    11 Nov 2019{" "}
                                    <span className="d-block text-info">
                                      10.00 AM
                                    </span>
                                  </td>
                                  <td>
                                    <div className="table-action">
                                      <a
                                        href="#edit_medical_form"
                                        onClick={() =>
                                          handleShow("medical_details")
                                        }
                                        className="btn btn-sm bg-info-light"
                                        data-toggle="modal"
                                      >
                                        <i className="fas fa-edit" /> Edit
                                      </a>
                                      <a
                                        href="#"
                                        className="btn btn-sm bg-danger-light"
                                      >
                                        <i className="fas fa-trash-alt" />{" "}
                                        Delete
                                      </a>
                                    </div>
                                  </td>
                                </tr>
                                <tr>
                                  <td>1</td>
                                  <td>Richard Wilson</td>
                                  <td>23.7</td>
                                  <td className="text-center">89</td>
                                  <td className="text-center">140</td>
                                  <td>74Kg</td>
                                  <td>
                                    11 Nov 2019{" "}
                                    <span className="d-block text-info">
                                      10.00 AM
                                    </span>
                                  </td>
                                  <td>
                                    <div className="table-action">
                                      <a
                                        href="#edit_medical_form"
                                        onClick={() =>
                                          handleShow("medical_details")
                                        }
                                        className="btn btn-sm bg-info-light"
                                        data-toggle="modal"
                                      >
                                        <i className="fas fa-edit" /> Edit
                                      </a>
                                      <a
                                        href="#"
                                        className="btn btn-sm bg-danger-light"
                                      >
                                        <i className="fas fa-trash-alt" />{" "}
                                        Delete
                                      </a>
                                    </div>
                                  </td>
                                </tr>
                              </tbody>
                            </table>
                          </div>
                        </div>
                      </div>
                      {/* /Dependent Tab */}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        show={show}
        onHide={handleClose}
        aria-labelledby="example-modal-sizes-title-sm"
      >
        <Modal.Header>
          <h5 className="modal-title">Add new data</h5>
          <button
            type="button"
            className="close"
            data-dismiss="modal"
            aria-label="Close"
            onClick={handleClose}
          >
            <span aria-hidden="true">×</span>
          </button>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-body">
            <div>
              <form
                action="#"
                enctype="multipart/form-data"
                autocomplete="off"
                method="post"
              >
                <div className="modal-body">
                  <input type="hidden" defaultValue name="id" />
                  <input type="hidden" defaultValue="insert" name="method" />
                  <div className="form-group">
                    <label className="control-label mb-10">
                      {" "}
                      BMI <span className="text-danger">*</span>
                    </label>
                    <input
                      type="text"
                      name="bmi"
                      className="form-control"
                      defaultValue="23.7"
                    />
                  </div>
                  <div className="form-group">
                    <label className="control-label mb-10">Heart rate </label>
                    <input
                      type="text"
                      name="hr"
                      className="form-control"
                      defaultValue={89}
                    />
                  </div>
                  <div className="form-group">
                    <label className="control-label mb-10">Weight</label>
                    <input
                      type="text"
                      name="Weight"
                      className="form-control"
                      defaultValue={74}
                    />
                  </div>
                  <div className="form-group">
                    <label className="control-label mb-10">Fbc</label>
                    <input
                      type="text"
                      name="Fbc"
                      className="form-control"
                      defaultValue={140}
                    />
                  </div>
                  <div className="form-group">
                    <label className="control-label mb-10">Creation Date </label>
                    <input
                      type="text"
                      name="dob"
                      id="editdob"
                      defaultValue="11/11/2019"
                      readOnly
                      className="form-control"
                    />
                  </div>
                </div>
                <div className="modal-footer text-center">
                  <button
                    type="submit"
                    className="btn btn-outline btn-success "
                  >
                    Submit
                  </button>
                </div>
              </form>
            </div>
          </div>
        </Modal.Body>
      </Modal>
    </div>
  );
}
export default Specialities;
